<template>
    <div class="header">
        <div  @click="goTo('home')" class="logo">
            <h4 class="paw">Pet Paw</h4>
            <img src="../../assets/img/catfood3.jpg" width="45px" height="45px" style="margin-left:10px">
        </div>
        <div class="d-flex cart">
            <i @click="goTo('cart')" class="fa fa-shopping-cart cart-icon"></i>
            <span class="cart-count">{{count}}</span>
            <h5 class="register">Login</h5>
            <h5 class="login">Sign up</h5>
        </div>
    </div>
</template>
<script>
export default {
    name:'Header',
    methods:{
        goTo(page){
            page === 'home' ? this.$router.push('/'): this.$router.push("/cart")
        }
    },
    computed:{
        count(){
            return this.$store.state.cartItemCount
        }
    }
}
</script>
