import catfood1  from '../assets/img/catfood1.jpg'
import catfood2  from '../assets/img/catfood2.jpg'
import catfood3  from '../assets/img/catfood3.jpg'
import catplaying1  from '../assets/img/catplaying1.jpg'
import catdressing1 from '../assets/img/catdressing1.jpg'
import catdressing2 from '../assets/img/catdressing2.jpg'
import cathousing1 from '../assets/img/cathousing1.jpg'
import cathousing2 from '../assets/img/cathousing2.jpg'
import cathousing3 from '../assets/img/cathousing3.jpg'



export default new Vuex.Store({
  state: {
    topRated:[
      {id: 0-1, name:'', price:100, url: catfood1, desc:"", rate: 4.7 },
      {id: 0-2, name:'', price:299, url:catfood2, desc:"", rate:4.6}

    ],

    allCategories: [
      {id: 1-1, name:'', price:100, url: catfood1, desc:"", rate: 4.7 },
      {id: 1-2, name:'', price:299, url:catfood2, desc:"", rate:4.6},
      {id: 1-3, name:'', price:19.9, url:catfood3, desc:"", rate: 4.5},
      {id: 1-4, name:'', price:9.9, url:catdressing1, desc:"", rate: 4.8},
      {id: 1-5, name:'', price:19.9, url:catdressing2, desc:"", rate: 4.5},
      {id: 1-6, name:'1', price:59.9, url:cathousing1, desc:"", rate: 4.8},
      {id: 1-7, name:'2', price:69.9, url:cathousing2, desc:"", rate: 4.7},
      {id: 1-8, name:'3', price:169.9, url:cathousing3, desc:"", rate: 4.5},
      {id: 1-9, name:'', price:189.9, url:catplaying1, desc:"", rate: 4.6},
    ],
    petfood:[
      {id: 2-1, name:'', price:100, url: catfood1, desc:"", rate: 4.7 },
      {id: 2-2, name:'', price:299, url:catfood2, desc:"", rate:4.6},
      {id: 2-3, name:'', price:19.9, url:catfood3, desc:"", rate: 4.5},
    ],
    petdressing:[
      {id: 3-1, name:'', price:9.9, url:catdressing1, desc:"", rate: 4.8},
      {id: 3-2, name:'', price:19.9, url:catdressing2, desc:"", rate: 4.5},
    ],
    petplaying:[
      {id: 4-1, name:'', price:189.9, url:catplaying1, desc:"", rate: 4.6},

    ],
    pethousing:[
      {id: 5-1, name:'1', price:59.9, url:cathousing1, desc:"", rate: 4.8},
      {id: 5-2, name:'2', price:69.9, url:cathousing2, desc:"", rate: 4.7},
      {id: 5-3, name:'3', price:169.9, url:cathousing3, desc:"", rate: 4.5},
    ],
    cartItemCount:0,
    cartItems:[]
  },
  mutations:{
    changeToCart(state, {payload, num}){
      let item = payload
      item = {...item, quantity:1}
      //
      if(state.cartItems.length > 0){
        //
        let bool = state.cartItems.some(i => i.id == item.id)
        if(bool){
          let itemIndex = state.cartItems.findIndex(el => el.id === item.id)
          
          state.cartItems[itemIndex]["quantity"] += num
          
          if(state.cartItems[itemIndex]["quantity"] == 0){
            // 0
            state.cartItems.pop(item)
          }
        }else {
          //
          state.cartItems.push(item)
        }
      }else{
        // 
        state.cartItems.push(item)
      }
      state.cartItemCount+= num
    }
  },
  actions:{
    changeToCart:({ commit }, {payload, num}) =>{
      commit("changeToCart", {payload, num})
    }
  }
})

